import React from 'react';
import Flower from './components/Flower';
import './App.css';

const flowers = [
  {
    name: '',
    image: 'https://balthazar.club/uploads/posts/2023-02/1675201715_balthazar-club-p-rozi-tsveti-krasivo-7.jpg',  //     
    description: '   ,   '
  },
  {
    name: '',
    image: 'https://avatars.mds.yandex.net/i?id=cc8da4da248ac2e415242738999cfd9bc4f95f4f6a028c95-12624498-images-thumbs&n=13',  
    description: '   .'
  },
  {
    name: '',
    image: 'https://klau.club/uploads/posts/2023-06/1685992853_klau-club-p-sinie-pioni-1.jpg',  
    description: ' ).'
  },
  {
    name: '',
    image: 'https://vash-aromat.ru/_pu/2/23731018.jpg',  
    description: '     .    -   .'
  },
  {
    name: '',
    image: 'data:image/png;base64,',  
    description: '      )  !.'
  },
  //    
];

const App = () => {
  return (
    <div className="app">
      <header>
        <h1>Flower Gallery</h1>
      </header>
      <main>
        <div className="gallery">
          {flowers.map((flower, index) => (
            <Flower
              key={index}
              name={flower.name}
              image={flower.image}
              description={flower.description}
            />
          ))}
        </div>
      </main>
    </div>
  );
};

export default App;
